.item {
    padding:6px 6px 6px 50px;
    position: relative;
    margin-bottom: 12px;
    border: 1px solid #e4e9f0;
    border-radius: 4px;
    box-shadow: 1px 1px 2px 1px rgba(191,205,222,0.74);
    cursor: pointer;
    background-color: #fff;
}
.item:last-child {
    margin-bottom: 0;
}
.statusOnline {
    position: absolute;
    left: 25px;
    top: 50%;
    transform: translate(-50%,-50%);
    width:16px;
    height: 16px;
    border-radius: 50%;
    overflow: hidden;
    background-color: green;
}
.statusOffline {
    composes: statusOnline;
    background-color: red;
}
.avatar {
    vertical-align:middle;
    border-radius: 6px;
    margin-right: 12px;

}
.name {
    display: inline;
    margin: 0;
    font-size: 18px;
    font-family: 'Montserrat', sans-serif;
    color: #475965;
    font-weight: 700;
}